import React,{ useRef } from 'react';
import { useIntersection } from 'react-use';
import './hiking.css';
import { Container, Row, Col } from 'react-bootstrap';
import hiking from '../images/hiking.png';
import gsap from 'gsap'

export default function Hiking() {

let hikerHeader = useRef(null)

const intersection = useIntersection(hikerHeader, {
  root: null,
  rootMargin: "0px",
  threshold: 0.5
} );

const fadeIn = element => {
gsap.to(element, 1, {
  opacity: 1,
  y: -30,
  ease: "power4.out",
  stagger : {
    amount: 0.3
  }
});
};

const fadeOut = element => {
  gsap.to(element, 1, {
    opacity: 0,
    y: -20,
    ease: "power4.out",
  });
  };

  intersection && intersection.intersectionRatio < 0.5
  ? fadeOut(".fadeIn")
  : fadeIn(".fadeIn")

  return (
    <div className="hiking pt-5">
      <h3 className="hikingHeading ">Amazing online and live courses</h3>

      <Container>
        <Row>
          <Col xs={12} sm={12} md={12} lg={4} className="hikingImage">
            <h3 className="hikerTwo">
              HIKING
          </h3>
            <img src={hiking} className="hik pt-5" alt="hiker" />
          </Col>
          <Col xs={12} sm={12} md={12} lg={8} className="writing allin"
          ref={hikerHeader}
          >
            <h3 className=" hikerParagraphHeader pb-4 fadeIn">
              ABOUT HIKING
            </h3>
            <p className="hikerParagraph fadeIn"
            
            >
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor
              incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud
              exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure
              dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur.
              Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt
                   mollit anim id est laborum.<br /><br />
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor
                     incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud
                      exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure
                       dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur.
                        Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
              </p>

            <button className="hikerButton fadeIn">
              Start
                </button>
          </Col>
        </Row>
      </Container>

    </div>
  )
}
