import React from 'react';
import './index.css';
import NavBar from '../Navigation/NavBar';
import { Container, Row, Col } from 'react-bootstrap';

function Index() {
  return (
    <div className="banner">
      <NavBar />
      <Container>
        <Row className="justify-content-center">
          <Col className="xs={{ span: 6, offset: 3 }} md={{ span: 6, offset: 3 }}  lg={{ span: 6, offset: 3 }} firstText">
            <p >
              We promote <span className="knowledge">KNOWLEDGE</span> beyond your wildest dreams
     </p>
            <button className="joinButton">
              Join Free
            </button>

            <p className="secondText">
            The world’s largest selection of Outdoor educational courses <br/> Choose from over 100,000 online video courses with new<br/> additions published every month.
              </p>
          </Col>
        </Row>
      </Container>
    </div>
  )
}

export default Index;