import React from 'react';
import './experience.css';
import { Container, Row, Col } from 'react-bootstrap';
import lower from '../images/lower.png';
import exT from '../images/exT.png';
import LEARN from '../images/LEARN.png';

export default function Experience() {
    return (
        <div className="experience">
            <Container>
                <Row>
                    <Col xs={12} sm={12} md={12} lg={12}>
                        <h1 className="experienceHeader pb-5">
                            WE OFFER THE RIGHT EXPERIENCE
                         </h1>
                    </Col>
                    <Col xs={12} sm={12} md={12} lg={4}>
                        <img src={lower} className="hiki pt-5" alt="Lower" />
                        <h5 className="titled pb-2">
                            <b>Lower Risk Level</b>
                        </h5>
                        <p className="para">
                            Our quality curriculum is designed with top-tier industry partners, not academics, so you learn the high-impact skills that top companies want.
                            </p>
                    </Col>
                    <Col xs={12} sm={12} md={12} lg={4}>
                        <img src={exT} className="hiki pt-5" alt="experienced" />
                        <h5 className="titled pb-2">
                            <b>Experienced Tutors</b>
                        </h5>
                        <p className="para">
                            Our quality curriculum is designed with top-tier industry partners, not academics, so you learn the high-impact skills that top companies want.
                            </p>
                    </Col>
                    <Col xs={12} sm={12} md={12} lg={4}>
                        <img src={LEARN} className="hiki pt-5" alt="Learn" />
                        <h5 className="titled pb-2">
                            <b>Learn on your Schedule</b>
                        </h5>
                        <p className="para">
                            Our quality curriculum is designed with top-tier industry partners, not academics, so you learn the high-impact skills that top companies want.
                            </p>
                    </Col>
                </Row>
                </Container>
                
              
        </div>
    )
}
