import React from 'react';
import './discover.css';
import swim from '../images/swim.png';
import car from '../images/car.png';
import climb from '../images/climb.png';
import fish from '../images/fish.png';
import bigman from '../images/bigman.png';
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';

export default function Discover() {
    return (
        <div className="discover pb-5">
            <h3 className="pt-5 pb-5">
               <b> DISCOVER YOUR PERFECT PROGRAM </b>
            </h3>
            <OwlCarousel
                items={3}
                className="owl-theme pt-5"
                margin={20}
                stagePadding={150}
                dots={true}
                loop={true}
                slideBy={"page"}
                dotsEach={false}
                navText={["<i class='mdi mdi-chevron-left'></i>", "<i class='mdi mdi-chevron-right'></i>"]}
                responsive={{
                    0: {
                        items: 1,
                        stagePadding: 40,
                    },
                    420: {
                        items: 1,
                        stagePadding: 60,
                    },
                    768: {
                        items: 1,
                        stagePadding: 80,
                    },
                    1024: {
                        items: 2,
                        nav: true,
                    },
                    1280: {
                        items: 3,
                        nav: true,
                        mouseDrag: false
                    }
                }
                }>
                <div className="Had"> 
                 <span className="Head-overlay">
            </span>
            <img alt="some" /></div>
                <div className="climb">
                 <h3>
                    Swiming
                     </h3>
                     <p>
                    
                         </p>
                </div>
                <div className="Had">
                    
                </div>
                <div className="climb">

                </div>
                <div className="Had">

                </div>
            </OwlCarousel>

            {/* <div className="manBig">
                <img src={bigman} alt="some"
                    style={{
                        width: "100%",
                        height: "100%",
                        position: "relative",
                        marginTop: "-45%",
                        zIndex: "3000"
                    }} />
            </div> */}

            <button className="startButton mt-5 mb-5">
                  Start
                </button>
        </div>
    )
}
