import React from 'react';
import './Nav.css'
import { Navbar, Nav } from 'react-bootstrap';

function NavBar() {
  return (
    <div>
      <Navbar collapseOnSelect expand="lg" bg="transparent" variant="transparent">
  <Navbar.Brand href="#home" className="logo ml-5">
    
  </Navbar.Brand>
  <Navbar.Toggle aria-controls="responsive-navbar-nav" className="toggle"/>
  <Navbar.Collapse id="responsive-navbar-nav">
    <Nav className="m-auto">
     
    </Nav>
    <Nav className="mr-5 navitems">
      <Nav.Link href="#deets" className="white ft">Home</Nav.Link>
      <Nav.Link eventKey={2} href="#memes" className="white ft">
        Pricing
      </Nav.Link>
      <Nav.Link href="#deets" className="white ft">Courses</Nav.Link>
      <Nav.Link href="#deets" className="white ft">Account</Nav.Link>
    </Nav>
  </Navbar.Collapse>
</Navbar>
    </div>
  )
}

export default NavBar;